import React, { Component } from "react";
import './About.css';
import Fade from 'react-reveal/Fade';

class About extends Component {
    render() {
      return (
        <div class aboutPage>
          <h1>
            <Fade bottom cascade><div className="about-title">About Me!</div></Fade> 
          </h1>
          
          <h2>
            I'm a fullstack web developer focused on React.JS, JavaScript, Node.JS and Java. I have spent a lot of time learning to code through a variety of sources including a fullstack, full-time web development bootcamp using the PERN stack (PostgreSQL, Express, React.JS, Node.JS, and JavaScript of course). In my free time I have worked on learning a variety of other technologies such as Java, MongoDB, C/C++, and Python.
          </h2> 


        </div>
      );
    }
  }
  
  export default About;